 .main-article-header ul li a i {
    padding-top: 0.4rem;
}

 .main-article-header ul li .active {
    color: rgb(64, 163, 244);
}

 .main-article-header ul .active:after {
    display: block;
    content: '';
    border-bottom: 3px solid rgb(64, 163, 244);
    width: 97%;
    margin: auto;
}

 .main-article-header ul li a:hover{
    color: white;
    background: rgb(64, 163, 244);
    border-radius: 5px;
    border: 1px solid white;
    box-shadow: 0 0 14px 1px rgba(0, 0, 0, 0.3);
    z-index: 5;
}

 .main-article-header ul .active:hover:after {
    display: none;
}

@media screen and (max-width: 400px) {

    .main-article-header ul{
        flex-direction: column;
        padding: 0.8rem 0;

    }
    .main-article-header ul li{
        display: block;
        flex: 1 1 100%;
        // border: 1px solid rgb(64, 163, 244);
        width: 50%;
    }
}

